  border-radius: 5px;
  background-color: #b2120f;
  cursor: pointer;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  -ms-transition: all 0.3s;
  transition: all 0.3s;
}

.btn.active, .btn:active {
  -webkit-box-shadow: none;
  box-shadow: none;
}

.btn:focus, .btn:active:focus, .btn.active:focus {
  outline: none;
}

.btn:hover, .btn:active, .btn.hover, .btn.active {
  color: #b2120f;
  background-color: transparent;
}

.btn i:first-child {
  margin-right: 5px;
}

.btn i:last-child {
  margin-left: 5px;
}

.btn.btn-transparent {
  color: #b2120f;
  background-color: transparent;
}

.btn.btn-transparent:hover, .btn.btn-transparent:active, .btn.btn-transparent.hover, .btn.btn-transparent.active {
  background-color: #b2120f;
  color: #fff;
}

/*********************
  3. Button Group
**********************/
.btn-group {
  margin: 10px;
  display: flex;
  justify-content: flex-end;
}

/*********************
  4. Disabled Buttons
**********************/
.btn:disabled {
  background: #ebebeb;
  border-color: #ebebeb;
  cursor: not-allowed;
  color: #555;
}

/*********************
  5. Social Buttons
**********************/
.social-buttons li {
  margin: 0 2px;
  float: left;
}

.social-buttons li a {
  -webkit-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  -ms-transition: all 0.2s ease;
  transition: all 0.2s ease;
  background: #f2f2f2;
  border-radius: 100%;
  display: block;
  height: 30px;
  line-height: 30px;
  width: 30px;
  text-align: center;
  color: #171717;
  text-decoration: none;
}

.social-buttons li img {
  width: 100%;
  height: 100%;
  border-radius: inherit;
}

.social-buttons li.facebook a:hover {
  background: #3B5998;
  color: #fff;
}

.social-buttons li.twitter a:hover {
  background: #1da1f2;
  color: #fff;
}

.social-buttons li.youtube a:hover {
  background: #e52d27;
  color: #fff;
}

.social-buttons li.instagram a:hover {
  background: #405de6;
  color: #fff;
}

.social-buttons li.linkedin a:hover {
  background: #0077b5;
  color: #fff;
}

.social-buttons li.github a:hover {
  background: #333;
  color: #fff;
}

.social-buttons li.url a:hover {
  background: #e38b24;
  color: #fff;
}

/*********************
  6. Available Buttons
**********************/
.available_button {
  display: inline-block;
  height: 45px;
  overflow: hidden;
  margin: 7px;
}

.available_button img {
  height: 100%;
}

/* ==========================================================================
   Lists
   ========================================================================== */
/**
 * Table of Contents:
 *
 *  1. Declarations
 *  2. General
 *  3. Breadcrumb
 *  4. Category List
 *  5. Portfolio List
 *  6. Check List
 *  7. Mail List
 */
/*********************
  1. Declarations
**********************/
/*********************
  2. General
**********************/
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

/*********************
  3. Breadcrumb
**********************/
ul.breadcrumb li {
  display: inline;
}

ul.breadcrumb li a {
  color: #d8d8d8;
  font-size: 0.9rem;
}

ul.breadcrumb li:not(:last-child) a:hover {
  text-decoration: underline;
}

ul.breadcrumb li:not(:last-child):not(:first-child):before {
  margin: 0 5px;
  content: "/";
}

ul.breadcrumb li:last-child {
  display: block;
  margin-top: 10px;
}

ul.breadcrumb li:last-child a {
  color: #fff;
  font-size: 2rem;
  border-bottom: 4px solid #b2120f;
  padding-bottom: 4px;
}

/*********************
  4. Category List
**********************/
ul.category-list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

ul.category-list li {
  color: #f2f2f2;
  margin: 10px;
}

ul.category-list li a {
  padding: 6px 12px;
  color: #d8d8d8;
  background: transparent;
  border-radius: 4px;
}

ul.category-list li a:hover, ul.category-list li.active a {
  background: #b2120f;
  color: #fff;
  background: #b2120f;
}

/*********************
  5. Portfolio List
**********************/
ul.portfolio-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

ul.portfolio-list > li.item {
  width: 270px;
  height: 270px;
  position: relative;
  margin: 10px;
}

ul.portfolio-list > li.item.large-item {
  width: 560px;
}

ul.portfolio-list > li.item > a {
  display: block;
  position: relative;
  border: 2px solid #fff;
  border-radius: 5px;
  overflow: hidden;
  width: 100%;
  height: 100%;
}

ul.portfolio-list > li.item .image {
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  -ms-transition: all 0.3s ease;
  transition: all 0.3s ease;
  border-radius: 3px;
  position: relative;
  height: 100%;
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
}

ul.portfolio-list > li.item .details {
  position: absolute;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  -ms-transition: all 0.3s;
  transition: all 0.3s;
  background: rgba(0, 0, 0, 0.8);
  color: #FFF;
  font-weight: 700;
  padding: 9px;
  text-transform: uppercase;
  z-index: 2;
  width: 100%;
  bottom: 0;
  display: flex;
  flex-direction: column;
}

ul.portfolio-list > li.item .categories > * {
  border-radius: 2px;
  font-size: 12px;
  font-weight: 400;
  margin: 2px;
  padding: 2px 9px;
  text-transform: none;
  z-index: 2;
  background: #b2120f;
}

ul.portfolio-list > li.item:hover .image {
  transform: scale(1.1, 1.1);
}

ul.portfolio-list > li.item:hover .details {
  background: black;
}

/*********************
  6. Check List
**********************/
ul.check-list li:before {
  content: "\f00c";
  font-family: FontAwesome;
  margin-right: 10px;
  color: #b2120f;
}

/*********************
  7. Mail List
**********************/
ul.mail-list li {
  padding: 5px 0 5px 40px;
  position: relative;
}

ul.mail-list li:before {
  content: "\f0e0";
  font-family: FontAwesome;
  color: #fff;
  background: #b2120f;
  border-radius: 50%;
  width: 18px;
  height: 18px;
  text-align: center;
  padding: 5px;
  position: absolute;
  left: 0;
  top: 5px;
}

/* ==========================================================================
   Inputs
   ========================================================================== */
/**
 * Table of Contents:
 *
 *  1. Declarations
 *  2. General
 *  3. Placeholder
 *  4. Form Group
 *  5. Form Group Addon
 */
/*********************
  1. Declarations
**********************/
/*********************
  2. General
**********************/
input,
textarea,
input[type="submit"]:focus,
button:focus {
  outline: none;
}

label,
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  vertical-align: top;
  margin: 0;
  border-radius: 0;
  font-size: inherit;
  line-height: normal;
}

input[type="submit"],
input[type="reset"],
input[type="button"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

input[type="checkbox"] {
  -webkit-appearance: checkbox;
  -moz-appearance: checkbox;
  appearance: checkbox;
  margin: 0;
}

input[type="radio"] {
  -webkit-appearance: radio;
  -moz-appearance: radio;
  appearance: radio;
  margin: 0;
}

.form-control {
  background-color: #fff;
  padding: 0 16px;
  font-size: 1rem;
  font-weight: 500;
  color: #1D2127;
  width: 100%;
  height: 35px;
  margin: 0;
  flex: 1;
  text-overflow: ellipsis;
  border: 1px solid #555;
  box-shadow: none !important;
  border-radius: 4px;
}

.form-control.has-addon {
  border-right: none;
}

textarea.form-control {
  padding: 14px 16px;
  min-height: 140px;
  resize: vertical;
  overflow: auto;
  text-transform: none;
}

.ie8 .form-control {
  line-height: 35px;
}

.form-control:focus {
  outline: none;
}

/*********************
  3. Placeholder
**********************/
:-moz-placeholder {
  font-size: 1rem;
  font-weight: 500;
  color: #d8d8d8;
}

::-moz-placeholder {
  font-size: 1rem;
  font-weight: 500;
  color: #d8d8d8;
}

:-ms-input-placeholder {
  font-size: 1rem;
  font-weight: 500;
  color: #d8d8d8;
}

::-webkit-input-placeholder {
  font-size: 1rem;
  font-weight: 500;
  color: #d8d8d8;
}

input:focus:-moz-placeholder {
  color: transparent !important;
}

input:focus::-moz-placeholder {
  color: transparent !important;
}

/*********************
  4. Form Group
**********************/
.form-group {
  position: relative;
  display: flex;
  margin: 25px 0 15px 0;
}

.form-group * {
  -webkit-transition: border-color ease-in-out 0.15s;
  -o-transition: border-color ease-in-out 0.15s;
  -ms-transition: border-color ease-in-out 0.15s;
  transition: border-color ease-in-out 0.15s;
  -webkit-transition: box-shadow ease-in-out 0.15s;
  -o-transition: box-shadow ease-in-out 0.15s;
  -ms-transition: box-shadow ease-in-out 0.15s;
  transition: box-shadow ease-in-out 0.15s;
}

.form-group label {
  font-size: 0.9rem;
  color: #f2f2f2;
  position: absolute;
  top: 0;
  left: 0;
  margin-top: -19.4444444444px;
}

.form-group label:after {
  display: block;
  content: "";
  position: absolute;
  top: 56px;
  opacity: 0;
  white-space: nowrap;
  transition: .2s opacity ease-out, .2s color ease-out;
}

.form-group.active label {
  color: #b2120f;
}

.form-group.active .form-control, .form-group.active .form-group-addon {
  border-color: #b2120f;
  box-shadow: none;
}

.form-group.has-success label:after, .form-group.has-error label:after {
  content: attr(data-message);
  opacity: 1;
  font-size: 0.8rem;
}

.form-group.has-error label {
  color: #d2322d;
}

.form-group.has-error label:after {
  color: #d2322d;
}

.form-group.has-error .form-control, .form-group.has-error .form-group-addon {
  border-color: #d2322d;
  box-shadow: none;
}

.form-group.has-success label {
  color: #47a447;
}

.form-group.has-success label:after {
  color: #47a447;
}

.form-group.has-success .form-control, .form-group.has-success .form-group-addon {
  border-color: #47a447;
  box-shadow: none;
}

